@use "../util" as *;

h1,
h2,
h3{
  margin-top: 0;
}
h1{
  @include breakpoint(large){
    font-size: rem(28);
  }
  font-size: rem(24);
  margin-bottom: rem(3);
  line-height: 1.1;
}

h2{
  font-size: rem(20);
  margin-bottom: rem(24);
}

a, a:visited, a:active{
  text-decoration: none;
}

