:root {
  --font-inter:"Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-mid: hsl(5, 77%, 71%) ;
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 15%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228,33%,91%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --background: var(--light-bg);
  --top-bg: var(--light-top-bg);
  --card:var(--light-card);
  --card-hover:var(--light-card-hover);
  --text1:var(--light-text1);
  --text2:var(--light-text2);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --top-bg: var(--dark-top-bg);
    --card:var(--dark-card);
    --card-hover:var(--dark-card-hover);
    --text1:var(--dark-text1);
    --text2:var(--dark-text2);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
                    225deg,
                    var(--toggle-bg-end) 0%,
                    var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text1: var(--light-text1);
  --text2: var(--light-text2);
  --card: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text1: var(--dark-text1);
  --text2: var(--dark-text2);
  --card: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
                  225deg,
                  var(--toggle-bg-end) 0%,
                  var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text2);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

@media (min-width: 71.875em) {
  h1 {
    font-size: 3rem;
  }
}

@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
  line-height: 1.1;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 68.75rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  margin: 0 auto 2.875rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card);
  color: var(--text2);
  width: 100%;
  border-radius: 0.3125rem;
  text-align: center;
  padding: 1.5625rem;
  cursor: pointer;
  transition: background 150ms ease-in-out;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-mid) 50.91%, var(--instagram-start) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
  height: 1.25rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text1);
}
.card__count {
  color: var(--text2);
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1;
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  color: var(--text1);
  font-weight: normal;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: bold;
  letter-spacing: normal;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3rem;
  grid-template-rows: repeat(2, 24px);
  gap: 0.3rem 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  grid-row: 1/2;
  justify-self: end;
}
.toggle label[for=light] {
  grid-column: 3/4;
  grid-row: 1/2;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px 0 -2px;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  z-index: 1;
  position: relative;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid var(--toggle-bg-end);
}
.toggle__background {
  display: block;
  border-radius: 0.75rem;
  position: absolute;
  height: 100%;
  width: 100%;
  transition: all 0.1s ease-out;
  top: 0;
  left: 0;
  z-index: 0;
  overflow: hidden;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  display: block;
  position: absolute;
  border-radius: 50%;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--toggle-button);
  transition: all 0.1s ease-out;
  z-index: 2;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 21px);
}

.header {
  display: flex;
  flex-wrap: wrap;
  gap: 0.625rem;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  padding-bottom: 1.875rem;
  border-bottom: 3px solid var(--text1);
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .header__title {
    border: none;
    padding: 0;
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text1);
}

.card-grid {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(2, auto);
  gap: 1.3125rem;
  align-items: center;
  justify-items: start;
  height: 7.8125rem;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid .card__change, .card-grid .card__icon {
  justify-self: end;
}